/**
 * websocketæ“ä½œç±»
 *
 * @param {WebSocket} scoket  è¿žæŽ¥
 * @param {Object}    options å®žä¾‹å‚æ•°
 * @param {Object}    func    å›žè°ƒæ–¹æ³•
 * @param {Number}    uid     å½“å‰ç™»å½•ç”¨æˆ·
 * @param {Number}    status  è¿žæŽ¥çŠ¶æ€ 0=æœªè¿žæŽ¥ 1=è¿žæŽ¥æˆåŠŸ
 */
import Notice from './noticeProcess'

const _args = {
  socket: Object,
  options: Object,
  func: Object,
  uid: Number,
  status: Number
}

var debug = function (e) {
  if (_args.options.debug) {
    console.log('%c' + e, 'color:#41B883')
  }
}

/**
 * init
 *
 * @param {Vue} Vue vueå®žä¾‹
 * @param {Object} options è¿žæŽ¥å‚æ•°
 * {
 *    debug: true, // æ˜¯å¦å¼€å¯debugè°ƒè¯•
 *    connection: 'ws://0.0.0.0:9502', // è¿žæŽ¥åœ°å€
 *    heartbeat: 5000 // å¿ƒè·³é—´éš”æ—¶é—´
 * }
 */
const install = function (Vue, options) {
  if (install.installed) return
  install.installed = true

  _args.options = options
  _args.status = 0

  /**
   * æ¯éš”10ç§’é‡è¿ž
   */
  var reconnect = function () {
    setTimeout(() => {
      if (_args.status === 0) {
        debug('reconnect')
        Vue.prototype.$ws.connect(_args.func, _args.uid)
      }
    }, 10000)
  }

  Vue.prototype.$ws = {
    /**
     * è¿žæŽ¥
     *
     * @param {Object} func å›žè°ƒæ–¹æ³•
     * @param {Number} uid  å½“å‰ç™»å½•ç”¨æˆ·uid
     */
    connect: function (func, uid) {
      _args.func = func
      _args.uid = uid
      _args.socket = new WebSocket(_args.options.connection)

      _args.socket.onopen = function (evt) {
        debug('conected success')
        _args.status = 1
        if (typeof func.onopen === 'function') {
          func.onopen(evt)
        }

        // å‘é€å¿ƒè·³
        Vue.prototype.$ws.heartbeat(uid)
      }

      // å®žä¾‹åŒ– onmessage
      _args.socket.onmessage = function (evt) {
        debug('ws-server-return-data:' + evt.data)
        if (typeof func.onmessage === 'function') {
          func.onmessage(evt.data)
        }
      }

      // onclose
      _args.socket.onclose = function (evt) {
        debug('close')
        _args.status = 0
        reconnect()
        if (typeof func.onclose === 'function') {
          func.onclose(evt)
        }
      }

      // onerror
      _args.socket.onerror = function (evt, e) {
        debug('error:' + evt.data)
        if (typeof func.onerror === 'function') {
          func.onerror(evt, e)
        }
      }
    },

    /** å‘é€æ•°æ® */
    send: function (str) {
      debug('send:' + str)
      _args.socket.send(str)
    },

    /** æ–­å¼€è¿žæŽ¥ */
    close: function () {
      debug('conected close')
      _args.socket.close()
    },

    /** å¿ƒè·³ */
    heartbeat: function (uid) {
      setInterval(() => {
        this.send(JSON.stringify({type: 'heartbeat', uid: uid, data: 'ping', timestamp: (new Date()).valueOf()}))
      }, options.heartbeat)
    },

    /**
     * æ¶ˆæ¯ç›‘æŽ§å¤„ç†ï¼ˆå•ç‹¬å¤„ç†ç±»ï¼‰
     */
    monitor: function (uid) {
      Notice.monitor(this, uid, _args)
    },

    notice: Notice
  }
}

export default {
  install
}



