import Vue from 'vue'
import VueResource from 'vue-resource'

Vue.use(VueResource)

const API_ROOT = 'http://swoole.luoio.com/api'

export default {
  setResource (user) {
    Vue.http.interceptors.push((request, next) => {
      request.headers.set('Accept', 'application/json')
      if (user) {
        request.headers.set('Token', user.token)
        request.headers.set('Authorization', 'Bearer ' + user.token)
      }
    })
  },

  // ç™»å½•
  signIn (data) {
    return Vue.resource(API_ROOT + '/user/login').get(data)
  },

  // æ³¨å†Œ
  signUp (data) {
    return Vue.resource(API_ROOT + '/user/register').get(data)
  },

  // é€€å‡ºç™»å½•
  logout () {
    return Vue.resource(API_ROOT + '/user/logout').get()
  },

  // è®¾ç½®ç”¨æˆ·ä¿¡æ¯
  setProfile (data) {
    return Vue.resource(API_ROOT + '/user/profile').save(data)
  },

  // èŽ·å–ç”¨æˆ·ä¿¡æ¯
  findUser (data) {
    return Vue.resource(API_ROOT + '/user/find').get(data)
  },

  // æ·»åŠ å¥½å‹
  friendsAdd (data) {
    return Vue.resource(API_ROOT + '/friends/add').save(data)
  },

  // å¤„ç†ç”³è¯·å¥½å‹è¯·æ±‚ (æ·»åŠ  or åˆ é™¤)
  friendsTask (data) {
    return Vue.resource(API_ROOT + '/friends/task').save(data)
  },

  // å¥½å‹åˆ—è¡¨
  friendsList () {
    return Vue.resource(API_ROOT + '/friends/list').save()
  },

  // åˆ é™¤å¥½å‹
  friendsRemove (data) {
    return Vue.resource(API_ROOT + '/friends/remove').save(data)
  }
}



